body{
	height: 100vh;
	width: 100vw;
	margin: 0 auto;
	background: white;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
	"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-webkit-font-smoothing: subpixel-antialiased;
	-moz-osx-font-smoothing: antialiased;
}

code{
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
	monospace;
}

/* General */

.App{
  text-align: center;
}

p{
	width: 70vw !important;
}

/* Banner */

.bannerContainer{
	width: 100vw;
	text-align: center;
	background-color: white;
    position: -webkit-sticky;
    position: -moz-sticky;
    position: -ms-sticky;
    position: -o-sticky;
    top: 0;
    display: inline-block;
}

.bannerBar{
    height: 1px;
	width: 100vw;
    background: -webkit-linear-gradient(300deg, navy -15%, #05d5ff 80%, #99ffcc 100%);
    background: linear-gradient(150deg, navy -15%, #05d5ff 80%, #99ffcc 100%);
	box-shadow: 0px 1px 2px rgba(0,0,0,.2);
}

.bannerTitlesContainer{
	padding-top: 6px;
	padding-bottom: 5px;
    width: 100vw;
    display: inline-block;
}

.bannerSubtitle{
    padding-left: 10px;
	font-size: 14px;
	font-family: bitter;
	font-weight: 300;
	font-style: italic;
	color: dimgrey;
}

.pearsonalLogo{
	color: black;
	background-color: white;
    border: 4px solid black;
	padding-left: 0.7vw;
	padding-right: 0.7vw;
	font-size: 3vh;
	font-family: bitter;
	font-weight: normal;
	text-transform: uppercase;
	-webkit-transition: 0.2s;
	transition: 0.2s;
	display: inline-block;
}

.pearsonalLogo:hover{
	color: #c82714;
    border: 4px solid #c82714;
}

/* Navigation */

.navigationContainer{
	width: 95vw;
	margin: 0 auto;
	padding: 0;
	text-align: center;
	list-style-type: none;
    display: -webkit-flex;
    display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
}

li{
	display: inline-block;
}

.noStyling{
	text-decoration: none;
	color: inherit;
}

.navButtons{
	cursor: pointer;
	width: 20vw;
	max-width: 150px;
	padding: 10px;
	text-align: center;
	font-family: bitter;
	font-weight: 400;
}

.navigationHome:hover{
	-webkit-transition: 0.2s;
	transition: 0.2s;
	color: navy;
	background-color: #BEBEED;
}

.navigationCards:hover{
	-webkit-transition: 0.2s;
	transition: 0.2s;
	color: darkgreen;
	background-color: #AFDFAF;
}

.navigationContact:hover{
	-webkit-transition: 0.2s;
	transition: 0.2s;
	color: #776505;
	background-color: #EAD878;
}

.navigationAbout:hover{
	-webkit-transition: 0.2s;
	transition: 0.2s;
	color: indigo;
	background-color: #DAA7FF;
}

/* Footer */

.footerContainer{
	width: 60vw;
	margin: 0 auto;
}

hr{
	width: 60vw;
	margin: 0 auto;
}

.footerText{
	padding: 15px;
	margin: 0 auto;
	font-family: avenir;
	font-size: 2vh;
	color: black;
	text-align: center;
}

/* Ritigos */

.ritigoContainer{
	width: 100vw !important;
}

.ritigo{
	-webkit-transform: skewY(-12deg);
	transform: skewY(-12deg);
	background-clip: border-box;
	box-sizing: border-box;
	display: block;
	z-index: -10;
}

.ritigoContainer > span{
	position: fixed;
}

.upper{
	height: 80px;
	width: 200px;
	top: 30%;
}

.middle{
	height: 100px;
	width: 450px;
	top: 50%;
}

.lower{
	height: 120px;
	width: 300px;
	top: 80%;
}

.upperRight{
	height: 120px;
	width: 300px;
	top: 20%;
	right: 0;
}

.middleRight{
	height: 100px;
	width: 450px;
	top: 50%;
	right: 0;
}

.lowerRight{
	height: 80px;
	width: 200px;
	top: 80%;
	right: 0;
}

/* Home */

.homeCanvas{
	max-width: 100vw;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
}

.homePearsonalLogo{
	margin: 50px;
	padding-left: 1.5vw;
	padding-right: 1.5vw;
	color: black;
	background-color: white;
    border: 6px solid black;
	font-size: 6vh;
	font-family: bitter;
	font-weight: normal;
	text-transform: uppercase;
}

.blurbContainer{
	width: 70vw;
	margin: 0 auto;
	margin-bottom: 25px;
	padding: 30px;
	border: 0.5px solid lightgrey;
	border-radius: 5px;
	background-color: white;
}

.blurbText{
	max-width: 100vw;
	font-family: avenir;
	font-size: 2.2vh;
	font-weight: lighter;
	line-height: 1.5;
	white-space: pre-wrap;
}

.subject{
	font-family: avenir;
	font-size: 3vh;
	font-weight: normal;
	display: inline;
}

.linkUnderlinePurple{
	color: black;
	-webkit-text-decoration-color: #b44efc;
	        text-decoration-color: #b44efc;
}

.linkUnderlineGreen{
	color: black;
	-webkit-text-decoration-color: #4dc076;
	        text-decoration-color: #4dc076;
}

.linkUnderlineYellow{
	color: black;
	-webkit-text-decoration-color: #ffd400;
	        text-decoration-color: #ffd400;
}

.linkUnderlineBlue{
	color: black;
	-webkit-text-decoration-color: none;
	        text-decoration-color: none;
}

.linkUnderlinePurple:hover{
	background-color: #DAA7FF;
	-webkit-transition: 0.2s;
	transition: 0.2s;
}

.linkUnderlineGreen:hover{
	background-color: #AFDFAF;
	-webkit-transition: 0.2s;
	transition: 0.2s;
}

.linkUnderlineYellow:hover{
	background-color: #EAD878;
	-webkit-transition: 0.2s;
	transition: 0.2s;
}

.linkUnderlineBlue:hover{
	background-color: #BEBEED;
	-webkit-transition: 0.2s;
	transition: 0.2s;
}

.ritigoLightBlue{
	background: -webkit-linear-gradient(left, #c1dcff 0%, #a0c9ff 100%);
	background: linear-gradient(90deg, #c1dcff 0%, #a0c9ff 100%);
}

.ritigoMediumBlue{
	background: -webkit-linear-gradient(left, #82b8ff 0%, #59a0ff 100%);
	background: linear-gradient(90deg, #82b8ff 0%, #59a0ff 100%);
}

.ritigoDarkBlue{
	background: -webkit-linear-gradient(left, #3088ff 0%, #0049ad 100%);
	background: linear-gradient(90deg, #3088ff 0%, #0049ad 100%);
}

.homeBackground{
	background-image: -webkit-linear-gradient(300deg, rgb(246, 249, 252) 30%, rgb(193, 220, 255) 100%);
	background-image: linear-gradient(150deg, rgb(246, 249, 252) 30%, rgb(193, 220, 255) 100%);
}

 /* Cards */

.cardsSummary{
	padding: 50px;
	margin: 0 auto;
	text-align: center;
	font-family: bitter;
	font-style: normal;
	font-size: 4vh;
	color: black;
}

.cardsContainer{
	max-width: 100vw;
	margin: 0 auto;
	border-radius: 5px;
}

.cardLayout{
	width: 80vw;
	padding: 10px;
	margin: auto;
	margin-bottom: 20px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-items: center;
	        align-items: center;
	vertical-align: middle;
	vertical-align: -webkit-baseline-middle;
	background-color: white;
	border: 0.5px solid lightgrey;
	text-align: center;
	border-radius: 5px;
}

.cardCover{
	margin: auto;
	height: auto;
	width: 25vw;
	max-width: 400px;
	margin-right: 5px;
	border: 1px solid green;
	border-radius: 3px;
}

.cardInside{
	margin: auto;
	height: auto;
	width: 25vw;
	max-width: 400px;
	margin-right: 5px;
	border: 1px solid red;
	border-radius: 3px;
}

.imageLayout{
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: flex-start;
	        justify-content: flex-start;
	overflow: scroll;
}

.additionalCardImages{
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
}

.cardYear{
	line-height: 1.5;
	text-align: center;
	font-family: pinyon script;
	font-size: 4.5vh;
}

.cardDescription{
	text-align: center;
	font-family: avenir;
	font-weight: lighter;
	font-size: 2.7vh;
}

.cardTextContainer{
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	min-width: 30vw;
	margin: auto;
}

.ritigoLightRed{
	background: -webkit-linear-gradient(left, #fc6a6a 0%, #f70202 100%);
	background: linear-gradient(90deg, #fc6a6a 0%, #f70202 100%);
}

.ritigoMediumGreen{
	background: -webkit-linear-gradient(left, #99ed68 0%, #4dc076 100%);
	background: linear-gradient(90deg, #99ed68 0%, #4dc076 100%);
}

.ritigoDarkGreen{
	background: -webkit-linear-gradient(left, #05af41 0%, #007f2d 100%);
	background: linear-gradient(90deg, #05af41 0%, #007f2d 100%);
}

.cardsBackground{
	background-image: -webkit-linear-gradient(300deg, rgb(246, 249, 252) 20%, rgba(77, 192, 118, 0.1) 100%);
	background-image: linear-gradient(150deg, rgb(246, 249, 252) 20%, rgba(77, 192, 118, 0.1) 100%);
}

/* Contact */

.contactCanvas{
	height: 90vh;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	vertical-align: middle;
	vertical-align: -webkit-baseline-middle;
}

.contactContainer{
	min-width: 40vw;
	max-width: 70vw;
	max-height: 80vh;
	max-height: 50vh;
	margin: 0 auto;
	margin-bottom: 25px;
	padding: 40px;
	border: 0.5px solid lightgrey;
	border-radius: 5px;
	background-color: white;
}

.contactSubject{
	text-align: center;
	font-family: avenir;
	font-size: 3vh;
	font-weight: normal;
}

.contactInputContainer{
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	margin: 0 auto;
	-webkit-align-items: center;
	        align-items: center;
}

.contactInput{
	width: 70%;
	font-size: 1.8vh;
	border: solid 1px #fce197;
	border-radius: 5px;
	margin-bottom: 15px;
	background-color: white;
}

.contactInput[type="submit"]{
	font-size: 1.8vh;
	padding: 5px;
	width: 120px;
}

.contactInput[type="submit"]:hover{
	width: 120px;
	cursor: pointer;
	background-color: #fce197;
	-webkit-transition: 0.2s;
	transition: 0.2s;
}

.gotchaHoneypot{
	visibility: hidden;
}

.ritigoLightYellow{
	background: -webkit-linear-gradient(left, #fff0aa 0%, #ffea87 100%);
	background: linear-gradient(90deg, #fff0aa 0%, #ffea87 100%);
}

.ritigoMediumYellow{
	background: -webkit-linear-gradient(left, #fce197 0%, #fcd669 100%);
	background: linear-gradient(90deg, #fce197 0%, #fcd669 100%);
}

.ritigoDarkYellow{
	background: -webkit-linear-gradient(left, #ffd400 0%, #ffd400 100%);
	background: linear-gradient(90deg, #ffd400 0%, #ffd400 100%);
}

.contactBackground{
	background-image: -webkit-linear-gradient(300deg, rgb(246, 249, 252) 50%, rgb(255, 240, 170) 100%);
	background-image: linear-gradient(150deg, rgb(246, 249, 252) 50%, rgb(255, 240, 170) 100%);
}

/* About  */

.aboutCanvas{
	max-width: 100vw;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
}

.aboutLinks{
	width: 60vw;
	margin: 10 auto;
	padding: 0;
	text-align: left;
	list-style-type: none;
    display: inline;
}

.aboutButtons{
	width: 10vw;
	cursor: pointer;
	padding: 10px;
	margin-right: 4vw;
	text-align: center;
	font-family: avenir;
	font-size: 80%;
	border: solid 1px #7a00bc;
	border-radius: 5px;
	color: #7a00bc;
}

.aboutButtons:hover{
	-webkit-transition: 0.2s;
	transition: 0.2s;
	color: white;
	background-color: #7a00bc;
}

.ritigoLightPurple{
	background: -webkit-linear-gradient(left, #ebd1ff 0%, #dcb3fc 100%);
	background: linear-gradient(90deg, #ebd1ff 0%, #dcb3fc 100%);
}

.ritigoMediumPurple{
	background: -webkit-linear-gradient(left, #c77aff 0%, #b44efc 100%);
	background: linear-gradient(90deg, #c77aff 0%, #b44efc 100%);
}

.ritigoDarkPurple{
	background: -webkit-linear-gradient(left, #7a00bc 0%, #6b00a5 100%);
	background: linear-gradient(90deg, #7a00bc 0%, #6b00a5 100%);
}

.aboutBackground{
	background-image: -webkit-linear-gradient(300deg, rgba(246, 249, 252, 0.1) 0%, rgba(199, 122, 255, .5) 100%);
	background-image: linear-gradient(150deg, rgba(246, 249, 252, 0.1) 0%, rgba(199, 122, 255, .5) 100%);
}

/* 404 Not Found */

.notFoundContainer{
	height: 90vh;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	vertical-align: middle;
	vertical-align: -webkit-middle;
}

.notFound{
	max-width: 40vw;
	padding: 100px;
	margin: 0 auto;
	font-size: 3vh;
	color: dimgrey;
	background-color: white;
	border: 0.5px solid lightgrey;
	border-radius: 5px;
	font-family: bitter;
	line-height: 2;
	text-align: center;
}

.ritigoLightGrey{
	background: gainsboro;
}

.ritigoMediumGrey{
	background: silver;
}

.ritigoDarkGrey{
	background: grey;
}

